.block__lk{
	display: flex;
	min-height: 85vh;
}

.block_lk__list{
	width: 40%;

	background-color: #F7F9FB;
}

.lk__list{
	list-style-type: none;
	display: flex;
	height: 100%;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
}

.lk__list > .lk__item {
	font-size: 24px;
	cursor: pointer;
	padding-left: 35px;
	width: 100%;
	height: 80px;
	line-height: 80px;
	position: relative;
	z-index: 2;
}

.lk__list > .lk__item.active{
	background-color: white;
}

.block_lk__info{
	width: 60%;
	background-color: white;
	border-radius: 10px;
	box-shadow: -5px 4px 10px rgba(140, 140, 140, 0.25);
}

.lk_profile__title{
	font-weight: normal;
font-size: 48px;
}